.toget {
    background: rgb(22, 22, 22);
    color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 30px 60px;
    overflow: hidden;
    position: relative;
    z-index: -1;
}
.toget::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: url(../img/background5.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 50% 10%;
    opacity: 0.3;
    z-index: -2;
}
.toget h3 {
    color: #B08126;
    font-weight: 400;
    font-size: 25px;
    width: 40%;
    margin: 20px;
    text-align: center;
}
.toget span {
    background: #D8AA35;
    width: 20%;
    height: 3px;
}
.toget h4 {
    margin: 30px;
    color: white;
    font-weight: 400;
    font-size: 28px;
}
.toget main {
    display: grid;
    justify-content: center;
    grid-template-columns: 20% 20% 20% 20%;
}
.toget .items {
    margin: 40px 25px;
    min-height: 200px;
    height: fit-content;
    font-size: 20px;
    line-height: 1.4;
    border-radius: 10px;
    background: #000000;
    display: flex;
    text-align: center;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}
.toget .items:hover {
    box-shadow: 0 0 10px 5px whitesmoke;
}
.toget .items img {
    margin: 10px 0;
}
@media only screen and (max-width: 1000px) {
    .toget main {
        width: 100%;
        grid-template-columns: 1fr;
    }
    .toget h3, .toget h4 {
        width: 100%;
        text-align: center;
    }
    .toget .items {
        padding: 10px;
    }
}